.best-card {
    box-shadow: 0px 8px 23px 0px #506B5221;
    border-radius: 16px;
    padding: 20px;
}

.best-card-link {
    position: relative;
    display: inline-block;
    margin-bottom: 16px;
}

.best-overlay {
    position: absolute;
    top: 0;
    left: 0;
    border-radius: 8px;
    width: 100%;
    height: 98%;
    background: #0000004D;
    display: flex;
    justify-content: center;
    align-items: center;
    color: var(--white);
    text-transform: uppercase;

}

.best-title {
    font-weight: 600;
    font-size: 24px;
    line-height: 1.5;
    max-width: 70%;
    letter-spacing: 0.2em;
    text-align: center;
}